"use client"
import React, { useState, useEffect } from 'react';
import { useRouter } from 'next/router';
import Link from 'next/link';
import { motion, AnimatePresence } from 'framer-motion';
import { ArrowRight, ArrowLeft, CheckCircle, X, Download, RotateCcw } from 'lucide-react';
import { v4 as uuidv4 } from 'uuid';

// Built-in UI Components (exactly matching your original)
const Card = ({ children, className = '' }) => (
  <div className={`bg-white border rounded-lg shadow-sm ${className}`}>
    {children}
  </div>
);

const CardContent = ({ children, className = '' }) => (
  <div className={`p-6 ${className}`}>
    {children}
  </div>
);

const Button = ({ children, onClick, disabled = false, variant = 'default', className = '' }) => {
  const baseClasses = 'inline-flex items-center justify-center px-4 py-2 rounded-md font-medium transition-colors focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed';
  
  const variantClasses = {
    default: 'bg-blue-600 text-white hover:bg-blue-700 focus:ring-blue-500',
    outline: 'border border-gray-300 bg-white text-gray-700 hover:bg-gray-50 focus:ring-blue-500'
  };

  return (
    <button
      onClick={onClick}
      disabled={disabled}
      className={`${baseClasses} ${variantClasses[variant]} ${className}`}
    >
      {children}
    </button>
  );
};

const Progress = ({ value, className = '' }) => (
  <div className={`w-full bg-gray-200 rounded-full h-2 ${className}`}>
    <div 
      className="bg-blue-600 h-2 rounded-full transition-all duration-300"
      style={{ width: `${value}%` }}
    />
  </div>
);

// Mock quiz questions (exactly from your original)
const mockQuizQuestions = [
  {
    id: 1,
    question: "What's your primary goal with drone technology?",
    options: [
      { id: 1, value: "hobby", text: "Personal hobby and recreation" },
      { id: 2, value: "career_change", text: "Career change to drone industry" },
      { id: 3, value: "business_expansion", text: "Expand existing business services" },
      { id: 4, value: "professional_skill", text: "Add professional skills" }
    ]
  },
  {
    id: 2,
    question: "Which industry interests you most?",
    options: [
      { id: 1, value: "agriculture", text: "Agriculture & Farming" },
      { id: 2, value: "construction", text: "Construction & Infrastructure" },
      { id: 3, value: "media", text: "Media & Filmmaking" },
      { id: 4, value: "surveying", text: "Surveying & Mapping" },
      { id: 5, value: "safety", text: "Public Safety & Emergency" },
      { id: 6, value: "mining", text: "Mining & Resources" }
    ]
  },
  {
    id: 3,
    question: "What's your current experience level?",
    options: [
      { id: 1, value: "beginner", text: "Complete beginner" },
      { id: 2, value: "some_experience", text: "Some recreational flying" },
      { id: 3, value: "intermediate", text: "Intermediate hobbyist" },
      { id: 4, value: "experienced", text: "Experienced pilot" }
    ]
  },
  {
    id: 4,
    question: "What's your preferred learning style?",
    options: [
      { id: 1, value: "hands_on", text: "Hands-on practical training" },
      { id: 2, value: "online", text: "Online self-paced learning" },
      { id: 3, value: "classroom", text: "Traditional classroom setting" },
      { id: 4, value: "mixed", text: "Combination of methods" }
    ]
  },
  {
    id: 5,
    question: "What's your budget range for training?",
    options: [
      { id: 1, value: "budget_conscious", text: "Under $1,000" },
      { id: 2, value: "moderate", text: "$1,000 - $2,500" },
      { id: 3, value: "premium", text: "$2,500 - $5,000" },
      { id: 4, value: "comprehensive", text: "Over $5,000 for comprehensive training" }
    ]
  }
];

// Local storage functions for Next.js (exactly from your original)
const saveQuizProgress = (answers) => {
  if (typeof window !== 'undefined') {
    localStorage.setItem('droneQuizAnswers', JSON.stringify(answers));
    localStorage.setItem('quizProgress', JSON.stringify(answers));
    localStorage.setItem('quizCompleted', 'true');
  }
};

const getQuizProgress = () => {
  if (typeof window !== 'undefined') {
    const saved = localStorage.getItem('droneQuizAnswers') || localStorage.getItem('quizProgress');
    return saved ? JSON.parse(saved) : {};
  }
  return {};
};

const clearQuizProgress = () => {
  if (typeof window !== 'undefined') {
    localStorage.removeItem('droneQuizAnswers');
    localStorage.removeItem('quizProgress');
    localStorage.removeItem('quizCompleted');
  }
};

const Quiz = () => {
  const router = useRouter();
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [answers, setAnswers] = useState({});
  const [selectedOption, setSelectedOption] = useState('');
  const [showCongratulationsPopup, setShowCongratulationsPopup] = useState(false);
  const [isDownloading, setIsDownloading] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: ''
  });
  const [agreeToShare, setAgreeToShare] = useState(true);

  useEffect(() => {
    const saved = getQuizProgress();
    if (Object.keys(saved).length > 0) {
      setAnswers(saved);
    }
  }, []);

  const progress = ((currentQuestion + 1) / mockQuizQuestions.length) * 100;
  const isLastQuestion = currentQuestion === mockQuizQuestions.length - 1;

  const handleAnswerSelect = (optionValue) => {
    setSelectedOption(optionValue);
  };

  const handleNext = () => {
    if (!selectedOption) return;

    const newAnswers = {
      ...answers,
      [mockQuizQuestions[currentQuestion].id]: selectedOption
    };
    setAnswers(newAnswers);
    saveQuizProgress(newAnswers);
    console.log('newAnswers :>> ', newAnswers);

    if (isLastQuestion) {
      setShowCongratulationsPopup(true);
    } else {
      setCurrentQuestion(currentQuestion + 1);
      setSelectedOption('');
    }
  };

  const handlePrevious = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1);
      setSelectedOption(answers[mockQuizQuestions[currentQuestion - 1].id] || '');
    }
  };

  const handleStartOver = () => {
    clearQuizProgress();
    setAnswers({});
    setCurrentQuestion(0);
    setSelectedOption('');
    setShowCongratulationsPopup(false);
    setFormData({ name: '', email: '', phone: '' });
    setAgreeToShare(true);
  };

  const handleFormChange = (field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const validateEmail = (email) => /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);

  const validatePhone = (phone) => {
    if (!phone || typeof phone !== 'string') {
      return false;
    }

    const cleaned = phone.replace(/[^\d+]/g, '');
    const numbers = cleaned.replace('+', '');
    
    if (numbers.length === 10) {
      return /^0[2-9]\d{8}$/.test(numbers);
    } else if (numbers.length === 11 && numbers.startsWith('61')) {
      const withoutCountryCode = numbers.substring(2);
      return /^[2-9]\d{8}$/.test(withoutCountryCode);
    }
    
    return false;
  };

  const toast = ({ title, description, variant }) => {
    alert(`${title}: ${description}`);
  };

  const handleDownloadPDF = async () => {
    if (!formData.name || !formData.email || !formData.phone) {
      toast({
        title: "Please fill all fields",
        description: "All fields are required to download your plan.",
        variant: "destructive"
      });
      return;
    }

    if (!validateEmail(formData.email)) {
      toast({
        title: "Invalid Email",
        description: "Please enter a valid email address.",
        variant: "destructive"
      });
      return;
    }

    if (!validatePhone(formData.phone)) {
      toast({
        title: "Invalid Phone Number",
        description: "Please enter a valid Australian phone number.",
        variant: "destructive"
      });
      return;
    }

    if (!agreeToShare) {
      toast({
        title: "Agreement Required",
        description: "Please agree to share your contact information to proceed.",
        variant: "destructive"
      });
      return;
    }

    setIsDownloading(true);
    
    try {
      // Save user data along with quiz answers
      const userData = {
        name: formData.name,
        email: formData.email,
        phone: formData.phone,
        timestamp: new Date().toISOString(),
        quizAnswers: answers
      };
      
      // Save to localStorage for the plan page to read
      if (typeof window !== 'undefined') {
        localStorage.setItem('userData', JSON.stringify(userData));
        localStorage.setItem('droneQuizAnswers', JSON.stringify(answers));
        localStorage.setItem('quizCompleted', 'true');
      }

      console.log('User data and quiz answers saved:', userData);

      // Simulate API call delay
      await new Promise(resolve => setTimeout(resolve, 2000));

      toast({
        title: "Success!",
        description: "Your personalized career plan is ready. Redirecting to your plan...",
      });

      setShowCongratulationsPopup(false);
      setFormData({ name: '', email: '', phone: '' });
      setAgreeToShare(true);
      
      // Navigate to plan page
      router.push('/plan');

    } catch (error) {
      console.error('Download error:', error);
      toast({
        title: "Download Failed",
        description: "Unable to process your request. Please try again.",
        variant: "destructive"
      });
    } finally {
      setIsDownloading(false);
    }
  };

  const isFormValid = formData.name && formData.email && formData.phone && agreeToShare;

  return (
    <div className="min-h-screen bg-white font-sans py-20 relative">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <h1 className="text-4xl font-bold text-slate-900 mb-4">
            Drone Career Assessment
          </h1>
          <p className="text-xl text-slate-600 mb-8 leading-relaxed">
            Discover your perfect drone career path in just 5 minutes
          </p>
          
          <div className="max-w-md mx-auto">
            <div className="flex justify-between text-sm text-slate-600 mb-3 font-medium">
              <span>Question {currentQuestion + 1} of {mockQuizQuestions.length}</span>
              <span>{Math.round(progress)}% Complete</span>
            </div>
            <Progress value={progress} className="h-2 bg-slate-100" />
          </div>
        </motion.div>

        {/* Question Card */}
        <AnimatePresence mode="wait">
          <motion.div
            key={currentQuestion}
            initial={{ opacity: 0, x: 20 }}
            animate={{ opacity: 1, x: 0 }}
            exit={{ opacity: 0, x: -20 }}
            transition={{ duration: 0.4 }}
            className="mb-12"
          >
            <Card className="border border-slate-200 rounded-2xl shadow-sm">
              <CardContent className="p-8">
                <h2 className="text-2xl font-bold text-slate-900 mb-8 leading-tight">
                  {mockQuizQuestions[currentQuestion].question}
                </h2>
                
                <div className="space-y-4">
                  {mockQuizQuestions[currentQuestion].options.map((option) => (
                    <button
                      key={option.id}
                      onClick={() => handleAnswerSelect(option.value)}
                      className={`w-full p-4 text-left rounded-xl border-2 transition-all duration-200 hover:scale-[1.02] focus:outline-none focus:ring-2 focus:ring-blue-500 ${
                        selectedOption === option.value
                          ? 'border-slate-900 bg-slate-50 text-slate-900'
                          : 'border-slate-200 bg-white hover:border-slate-300 hover:bg-slate-50'
                      }`}
                      role="radio"
                      aria-checked={selectedOption === option.value}
                    >
                      <div className="flex items-center">
                        <div className={`w-5 h-5 rounded-full border-2 mr-4 flex items-center justify-center transition-colors ${
                          selectedOption === option.value
                            ? 'border-slate-900 bg-slate-900'
                            : 'border-slate-300'
                        }`}>
                          {selectedOption === option.value && (
                            <div className="w-2 h-2 rounded-full bg-white" />
                          )}
                        </div>
                        <span className="font-medium text-base leading-relaxed">{option.text}</span>
                      </div>
                    </button>
                  ))}
                </div>
              </CardContent>
            </Card>
          </motion.div>
        </AnimatePresence>

        {/* Navigation */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6, delay: 0.3 }}
          className="flex justify-between items-center"
        >
          <Button
            onClick={handlePrevious}
            disabled={currentQuestion === 0}
            variant="outline"
            className="px-6 py-3 rounded-lg disabled:opacity-50 disabled:cursor-not-allowed border-slate-300 text-slate-700 hover:bg-slate-50 font-medium min-h-[44px]"
          >
            <ArrowLeft className="mr-2 w-4 h-4" />
            Previous
          </Button>

          <div className="text-center">
            <Link
              href="/"
              className="text-slate-500 hover:text-slate-700 transition-colors duration-200 font-medium"
            >
              Exit Assessment
            </Link>
          </div>

          <Button
            onClick={handleNext}
            disabled={!selectedOption}
            className="bg-slate-900 hover:bg-slate-800 text-white px-6 py-3 rounded-lg transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed font-medium min-h-[44px]"
          >
            {isLastQuestion ? 'Complete' : 'Next'}
            <ArrowRight className="ml-2 w-4 h-4" />
          </Button>
        </motion.div>

        {/* Progress Indicator Dots */}
        <motion.div
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.6, delay: 0.5 }}
          className="flex justify-center mt-12"
        >
          <div className="flex space-x-2">
            {mockQuizQuestions.map((_, index) => (
              <div
                key={index}
                className={`w-2 h-2 rounded-full transition-colors duration-300 ${
                  index < currentQuestion
                    ? 'bg-green-500'
                    : index === currentQuestion
                    ? 'bg-slate-900'
                    : 'bg-slate-300'
                }`}
              />
            ))}
          </div>
        </motion.div>
      </div>

      {/* Congratulations Popup */}
      <AnimatePresence>
        {showCongratulationsPopup && (
          <>
            {/* Backdrop */}
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4"
              onClick={() => setShowCongratulationsPopup(false)}
            />
            
            {/* Popup */}
            <motion.div
              initial={{ opacity: 0, scale: 0.8, y: 20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.8, y: 20 }}
              transition={{ duration: 0.3 }}
              className="fixed inset-0 z-50 flex items-center justify-center p-4"
            >
              <Card className="w-full max-w-md bg-white rounded-2xl shadow-2xl">
                <CardContent className="p-8 relative">
                  {/* Close Button */}
                  <button
                    onClick={() => setShowCongratulationsPopup(false)}
                    className="absolute top-4 right-4 p-2 rounded-full hover:bg-gray-100 transition-colors"
                  >
                    <X className="w-5 h-5 text-gray-500" />
                  </button>

                  {/* Success Icon */}
                  <div className="text-center mb-6">
                    <div className="inline-flex p-4 rounded-full bg-green-50 mb-4 border border-green-200">
                      <CheckCircle className="w-8 h-8 text-green-600" />
                    </div>
                    <h2 className="text-2xl font-bold text-slate-900 mb-2">
                      Congratulations!
                    </h2>
                    <p className="text-slate-600">
                      You have successfully completed the drone career assessment
                    </p>
                  </div>

                  {/* Form */}
                  <div className="space-y-4 mb-6">
                    <div>
                      <label htmlFor="name" className="block text-sm font-medium text-slate-700 mb-2">
                        Full Name *
                      </label>
                      <input
                        id="name"
                        type="text"
                        value={formData.name}
                        onChange={(e) => handleFormChange('name', e.target.value)}
                        className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        placeholder="Enter your full name"
                        required
                      />
                    </div>

                    <div>
                      <label htmlFor="email" className="block text-sm font-medium text-slate-700 mb-2">
                        Email Address *
                      </label>
                      <input
                        id="email"
                        type="email"
                        value={formData.email}
                        onChange={(e) => handleFormChange('email', e.target.value)}
                        className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        placeholder="Enter your email"
                        required
                      />
                    </div>

                    <div>
                      <label htmlFor="phone" className="block text-sm font-medium text-slate-700 mb-2">
                        Phone Number *
                      </label>
                      <input
                        id="phone"
                        type="tel"
                        value={formData.phone}
                        onChange={(e) => handleFormChange('phone', e.target.value)}
                        className="w-full px-4 py-3 border border-slate-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                        placeholder="Enter your phone number"
                        required
                      />
                    </div>

                    {/* Agreement Checkbox */}
                    <div className="flex items-start space-x-3 pt-2">
                      <input
                        id="agreeToShare"
                        type="checkbox"
                        checked={agreeToShare}
                        onChange={(e) => setAgreeToShare(e.target.checked)}
                        className="mt-1 w-4 h-4 text-blue-600 border-slate-300 rounded focus:ring-2 focus:ring-blue-500"
                      />
                      <label htmlFor="agreeToShare" className="text-sm text-slate-600 leading-relaxed">
                        I agree to share my contact info with drone training providers to discuss training options.
                      </label>
                    </div>
                  </div>

                  {/* Buttons */}
                  <div className="space-y-3">
                    <Button
                      onClick={handleDownloadPDF}
                      disabled={!isFormValid || isDownloading}
                      className="w-full bg-slate-900 hover:bg-slate-800 text-white py-3 rounded-lg font-medium transition-all duration-200 disabled:opacity-50 disabled:cursor-not-allowed"
                    >
                      <Download className="mr-2 w-4 h-4" />
                      {isDownloading ? 'Processing...' : 'Download Your Personalized PDF'}
                    </Button>
                    
                    <Button
                      onClick={handleStartOver}
                      variant="outline"
                      className="w-full border-slate-300 text-slate-700 hover:bg-slate-50 py-3 rounded-lg font-medium"
                    >
                      <RotateCcw className="mr-2 w-4 h-4" />
                      Start Over
                    </Button>
                  </div>
                </CardContent>
              </Card>
            </motion.div>
          </>
        )}
      </AnimatePresence>
    </div>
  );
};

export default Quiz;